.add-cell {
    position: relative;
    margin: 10px 0px;
    opacity: 0;
    transition: opacity 0.3s ease-in 0.1s;

}


.add-cell:hover {
    opacity: 1.0;
}

.force-visible .add-cell {
    opacity: 1;
}

.add-cell .divider {
    position: absolute;
    top: 10%;
    bottom: 50%;
    right: 5%;
    left: 5%;
    border-bottom: 1px solid gray;
    width: 90%;
    z-index: -1;
}

.add-cell .add-buttons {
    display: flex;
    justify-content: center;
}

.add-cell .add-buttons button {
    margin: 0 20px;
}